document.addEventListener('DOMContentLoaded', () => {
  const accordionHeaders = document.querySelectorAll('.cv-accordion-header');

  accordionHeaders.forEach(header => {
    header.addEventListener('click', () => {
      const targetId = header.dataset.target;
      const content = document.getElementById(targetId);

      if (content) {
        // –ü–µ—Ä–∞–∫–ª—é—á–∞–µ–º –∫–ª–∞—Å 'is-collapsed' –¥–ª—è –∑–º–µ—Å—Ü—ñ–≤–∞ (–ó–ú–ï–ù–ï–ù–ê)
        content.classList.toggle('is-collapsed');
        // –ü–µ—Ä–∞–∫–ª—é—á–∞–µ–º –∫–ª–∞—Å 'is-active' –¥–ª—è –∑–∞–≥–∞–ª–æ—û–∫–∞ (–¥–ª—è –ø–∞–≤–∞—Ä–æ—Ç—É —Å—Ç—Ä—ç–ª–∫—ñ)
        header.classList.toggle('is-active');

        // –ö–∞–ª—ñ –∑–º–µ—Å—Ü—ñ–≤–∞ —Ç–æ–ª—å–∫—ñ —à—Ç–æ –∞–¥–∫—Ä—ã–ª–∞—Å—è, –ø—Ä–∞–∫—Ä—É—á–≤–∞–µ–º –¥–∞ —è–≥–æ –∑–∞–≥–∞–ª–æ—û–∫–∞
        if (!content.classList.contains('is-collapsed')) {
          // –ó–ú–ï–ù–ï–ù–ê
          header.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
        }
      }
    });
  });

  // –Ü–Ω—ñ—Ü—ã—è–ª—ñ–∑–∞—Ü—ã—è: –ø–µ—Ä–∞–∫–∞–Ω–∞—Ü—Ü–∞, —à—Ç–æ "Profile" –∑–∞—û—Å—ë–¥—ã –∞–¥–∫—Ä—ã—Ç—ã –ø–∞ –∑–º–∞—û—á–∞–Ω–Ω—ñ, –∞—Å—Ç–∞—Ç–Ω—ñ—è - –∑–∞–∫—Ä—ã—Ç—ã
  document.querySelectorAll('.cv-content').forEach(content => {
    // –î–ª—è –∞–Ω–≥–ª—ñ–π—Å–∫–∞–π –≤–µ—Ä—Å—ñ—ñ
    if (content.id === 'eng-profile-content') {
      content.classList.remove('is-collapsed'); // –ó–ú–ï–ù–ï–ù–ê
      const header = document.querySelector(
        `.cv-accordion-header[data-target="${content.id}"]`
      );
      if (header) header.classList.add('is-active');
    }
    // –î–ª—è —û–∫—Ä–∞—ñ–Ω—Å–∫–∞–π –≤–µ—Ä—Å—ñ—ñ
    else if (content.id === 'ua-profile-content') {
      content.classList.remove('is-collapsed'); // –ó–ú–ï–ù–ï–ù–ê
      const header = document.querySelector(
        `.cv-accordion-header[data-target="${content.id}"]`
      );
      if (header) header.classList.add('is-active');
    } else {
      // –î–ª—è —û—Å—ñ—Ö –∞—Å—Ç–∞—Ç–Ω—ñ—Ö, –ø–µ—Ä–∞–∫–∞–Ω–∞–µ–º—Å—è, —à—Ç–æ —è–Ω—ã —Å—Ö–∞–≤–∞–Ω—ã—è
      if (!content.classList.contains('is-collapsed')) {
        // –ó–ú–ï–ù–ï–ù–ê
        content.classList.add('is-collapsed'); // –ó–ú–ï–ù–ï–ù–ê
      }
      const header = document.querySelector(
        `.cv-accordion-header[data-target="${content.id}"]`
      );
      if (header) header.classList.remove('is-active');
    }
  });
});
