// mobile-menu.js
(() => {
  const refs = {
    openModalBtns: document.querySelectorAll('[data-menu-open]'),
    closeModalBtns: document.querySelectorAll('[data-menu-close]'),
    // !!! –ó–ú–ï–ù–ï–ù–ê: –¶—è–ø–µ—Ä –º—ã –≤—ã–±—ñ—Ä–∞–µ–º –£–°–ï –º–∞–¥–∞–ª—å–Ω—ã—è –±—ç–∫–¥—Ä–æ–ø—ã !!!
    modalBackdrops: document.querySelectorAll('[data-menu]'),
    writeMeBtn: document.querySelector('.menu-writeme-btn'),
    modalWindow: document.querySelector('[data-modal]'), // –ì—ç—Ç–∞, –∑–¥–∞–µ—Ü—Ü–∞, —ñ–Ω—à–∞–µ –º–∞–¥–∞–ª—å–Ω–∞–µ –∞–∫–Ω–æ, –Ω–µ –º–µ–Ω—é
    menuButtons: document.querySelectorAll('.btn-list-item'),
    header: document.querySelector('.header-section'),
  };

  // –ê–¥–∫—Ä—ã—Ü—Ü—ë/–∑–∞–∫—Ä—ã—Ü—Ü—ë –º–µ–Ω—é
  // –ü—Ä–∞–≤–µ—Ä—Ü–µ, —Ü—ñ —ë—Å—Ü—å —Ö–æ—Ü—å –∞–¥–∑—ñ–Ω –±—ç–∫–¥—Ä–æ–ø
  if (refs.modalBackdrops.length > 0) {
    // –î–∞–¥–∞–µ–º —Å–ª—É—Ö–∞—á –¥–∞ –ö–û–ñ–ù–ê–ô –∫–Ω–æ–ø–∫—ñ –∞–¥–∫—Ä—ã—Ü—Ü—è
    refs.openModalBtns.forEach(button => {
      button.addEventListener('click', toggleActiveMenu);
    });

    // –î–∞–¥–∞–µ–º —Å–ª—É—Ö–∞—á –¥–∞ –ö–û–ñ–ù–ê–ô –∫–Ω–æ–ø–∫—ñ –∑–∞–∫—Ä—ã—Ü—Ü—è
    refs.closeModalBtns.forEach(button => {
      button.addEventListener('click', toggleActiveMenu);
    });
  }

  // !!! –ù–û–í–ê–Ø –§–£–ù–ö–¶–´–Ø: toggleActiveMenu - –∫–∞–± –∑–Ω–∞–π—Å—Ü—ñ –∞–∫—Ç—ã—û–Ω—ã –±—ç–∫–¥—Ä–æ–ø !!!
  function toggleActiveMenu() {
    // –ó–Ω–∞–π—Å—Ü—ñ –∞–∫—Ç—ã—û–Ω—ã –±—ç–∫–¥—Ä–æ–ø (—Ç–æ–π, —è–∫—ñ –ù–ï –º–∞–µ –∫–ª–∞—Å 'is-hidden' –∞–¥ language-switcher.js)
    let activeModal = null;
    refs.modalBackdrops.forEach(backdrop => {
      if (!backdrop.classList.contains('is-hidden')) {
        activeModal = backdrop;
      }
    });

    if (activeModal) {
      activeModal.classList.toggle('is-open');
      document.body.classList.toggle('no-scroll');
    }
  }

  // –õ–æ–≥—ñ–∫–∞ –¥–ª—è –∫–Ω–æ–ø–∫—ñ "–ù–∞–ø—ñ—Å–∞—Ü—å –º–Ω–µ" —û –º–µ–Ω—é
  if (refs.writeMeBtn && refs.modalWindow && refs.modalBackdrops.length > 0) {
    refs.writeMeBtn.addEventListener('click', () => {
      // –°—Ö–∞–≤–∞—Ü—å —É—Å–µ –º–∞–±—ñ–ª—å–Ω—ã—è –º–µ–Ω—é, –∫–∞–ª—ñ –∞–¥–∫—Ä—ã–≤–∞–µ—Ü—Ü–∞ —ñ–Ω—à–∞–µ –º–∞–¥–∞–ª—å–Ω–∞–µ –∞–∫–Ω–æ
      refs.modalBackdrops.forEach(backdrop => {
        backdrop.classList.remove('is-open');
      });
      document.body.classList.remove('no-scroll'); // –£–ø—ç—û–Ω—ñ–≤–∞–µ–º—Å—è, —à—Ç–æ —Å–∫—Ä–æ–ª —É–∫–ª—é—á–∞–Ω—ã
      refs.modalWindow.classList.add('is-open');
      refs.modalWindow.style.zIndex = '300';
    });
  }

  // –î–∞–¥–∞–µ–º –ø–∞–¥–∑–µ—é –Ω–∞ –∫–Ω–æ–ø–∫—ñ —û –º–µ–Ω—é –¥–ª—è —Å–∫—Ä–æ–ª–∞ —ñ –∑–∞–∫—Ä—ã—Ü—Ü—è –º–µ–Ω—é
  refs.menuButtons.forEach(button => {
    button.addEventListener('click', function () {
      const targetId = button.getAttribute('data-scroll');
      if (targetId) {
        document.querySelector(targetId).scrollIntoView({
          behavior: 'smooth',
        });
      }
      // –°—Ö–∞–≤–∞—Ü—å —É—Å–µ –º–∞–±—ñ–ª—å–Ω—ã—è –º–µ–Ω—é –ø–∞—Å–ª—è –∫–ª—ñ–∫—É –Ω–∞ –ø—É–Ω–∫—Ç –º–µ–Ω—é
      refs.modalBackdrops.forEach(backdrop => {
        backdrop.classList.remove('is-open');
      });
      document.body.classList.remove('no-scroll');
    });
  });

  // –§—ñ–∫—Å–∞—Ü—ã—è —Ö–µ–¥–µ—Ä–∞ –ø—Ä—ã —Å–∫—Ä–æ–ª–µ
  if (!refs.header) {
    console.error('‚ö† –•–µ–¥—ç—Ä-—Å–µ–∫—Ü—ã—è –Ω–µ –∑–Ω–æ–π–¥–∑–µ–Ω–∞! –ü—Ä–∞–≤–µ—Ä –∫–ª–∞—Å —É HTML.');
  } else {
    function checkScroll() {
      if (window.scrollY > 50) {
        refs.header.classList.add('fixed');
      } else {
        refs.header.classList.remove('fixed');
      }
    }
    window.addEventListener('scroll', checkScroll);
    checkScroll();
  }
})();
