import Typed from 'typed.js';

export function startTypedMultiline(elementId, strings, options = {}) {
  let currentLine = 0;
  const parent = document.getElementById(elementId);
  if (!parent) return;

  function typeNextLine() {
    if (currentLine >= strings.length) {
      if (options.loop) {
        currentLine = 0;
        parent.textContent = '';
        typeNextLine();
      }
      return;
    }
    const lineSpan = document.createElement('div');
    lineSpan.className = 'typed-multiline-line';
    parent.appendChild(lineSpan);

    new Typed(lineSpan, {
      ...options,
      strings: [strings[currentLine]],
      showCursor: currentLine === strings.length - 1,
      onComplete: () => {
        currentLine++;
        typeNextLine();
      },
    });
  }

  parent.textContent = '';
  typeNextLine();
}

export function showTypedTextByLang(lang) {
  if (lang === 'eng') {
    startTypedMultiline(
      'typed-text-eng',
      [
        'An aspiring Web Developer,',
        'creating modern web solutions,',
        'Ready to be your next team player',
        '________________________________________________',
        'Generations of people are working jobs they hate',
        'just to buy stuff they dont need. (Fight Club)',
        '________________________________________________',
        '‚Üì Watch my CV ‚Üì',
      ],
      {
        typeSpeed: 50,
        backSpeed: 25,
        backDelay: 1500,
        startDelay: 500,
        loop: false,
        showCursor: true,
        cursorChar: '|',
      }
    );
  } else if (lang === 'ua') {
    startTypedMultiline(
      'typed-text-ua',
      [
        '–í–µ–±-—Ä–æ–∑—Ä–æ–±–Ω–∏–∫-–ø–æ—á–∞—Ç–∫—ñ–≤–µ—Ü—å,',
        '—â–æ —Å—Ç–≤–æ—Ä—é—î —Å—É—á–∞—Å–Ω—ñ –≤–µ–±-—Ä—ñ—à–µ–Ω–Ω—è,',
        '–≥–æ—Ç–æ–≤–∏–π —Å—Ç–∞—Ç–∏ –≤–∞—à–∏–º –Ω–∞—Å—Ç—É–ø–Ω–∏–º —á–ª–µ–Ω–æ–º –∫–æ–º–∞–Ω–¥–∏.',
        '________________________________________________',
        '–ü–æ–∫–æ–ª—ñ–Ω–Ω—è –∑–∞ –ø–æ–∫–æ–ª—ñ–Ω–Ω—è–º–∏ –ª—é–¥–∏ –ø—Ä–∞—Ü—é—é—Ç—å –Ω–∞ –Ω–µ–Ω–∞–≤–∏—Å–Ω–∏—Ö —Ä–æ–±–æ—Ç–∞—Ö –ª–∏—à–µ –¥–ª—è —Ç–æ–≥–æ,',
        '—â–æ–± –º–∞—Ç–∏ –∑–º–æ–≥—É –∫—É–ø–∏—Ç–∏ —Ç–µ, —â–æ —ó–º –Ω–µ –ø–æ—Ç—Ä—ñ–±–Ω–æ. (–ë—ñ–π—Ü—ñ–≤—Å—å–∫–∏–π –∫–ª—É–±)',
        '________________________________________________',
        '‚Üì –ü–æ–¥–∏–≤—ñ—Ç—å—Å—è —Ä–µ–∑—é–º–µ. ‚Üì',
      ],
      {
        typeSpeed: 50,
        backSpeed: 25,
        backDelay: 1500,
        startDelay: 500,
        loop: false,
        showCursor: true,
        cursorChar: '|',
      }
    );
  }
}
